import React, {Component} from 'react';

class About extends Component{
	render(){
		return(
                 <div>
                      <section id="about" className="s-about">
          <div className="row section-header has-bottom-sep aos-init aos-animate" data-aos="fade-up">
            <div className="col-full">
              <h3 className="subhead subhead--dark">Hello There</h3>
              <h1 className="display-1 display-1--light">I am Manasi</h1>
            </div>
          </div> 
          <div className="row about-desc aos-init aos-animate" data-aos="fade-up">
            <div className="col-full">
              <p>
               A great developer and a designer in making, I am a  student at Cummins College of Engineering for Women, Pune. A creative thinker trying to find lasting solutions. A language lover at heart, I am a poetess and a film critic as well.
              </p>
            </div>
          </div> 
          <div className="row about-stats stats block-1-4 block-m-1-2 block-mob-full aos-init aos-animate" data-aos="fade-up">
            <div className="col-block stats__col ">
              <div className="stats__count">4</div>
              <h5>Poems</h5>
            </div>
            <div className="col-block stats__col">
              <div className="stats__count">3</div>
              <h5>Film Reviews</h5>
            </div>
            <div className="col-block stats__col">
              <div className="stats__count">Learns</div>
              <h5>Guitar</h5>
            </div>
            
            
          </div> 
          <div className="about__line" />
        </section>
                 </div>

			);
	}
}

export default About;