import React, {Component} from 'react';
import mee from './mee.JPG';

class Contact extends Component{
	render(){
		return(
               <section id="contact" className="s-contact">
                <div className="overlay" />
                  
                    <div className="row section-header aos-init" >
                     <div className="col-full">
                       <h3 className="subhead">Contact Me</h3>
                       <h1 className="display-2 display-2--light">Always looking out for Internships and new Opportunities!</h1>
                    </div>
                       
                   </div>
                   <div className="row contact-content aos-init" >
                     
                     <div className="contact-primary">
                     
                        <div className="contact-info">
                          <h3 className="h6 hide-on-fullwidth">Contact Info</h3>
                          <div className="pic">
                          <img src={mee}  alt="me"/>
                          </div>
                          <div className="cinfo">
                            <h5>Email  At</h5>
                              <p>
                               mgkhandekar@gmail.com.com<br />
                               manasi.khandekar@cumminscollege.in
                             </p>
                          </div>
                          <div className="cinfo">
                             
                            
                             
                          </div>
                           
                           <ul className="contact-social">
                             <li>
                               <a href="http://www.facebook.com/profile.php?id=100009451654660"><i className="fa fa-facebook" aria-hidden="true" />
                               </a>
                             </li>
                             <li>
                                <a href="http://www.instagram.com/libe_writer/"><i className="fa fa-instagram" aria-hidden="true" />
                                </a>
                             </li>
                             <li>
                                <a href="http://www.linkedin.com/in/manasi-khandekar-627772187"><i className="fa fa-linkedin" aria-hidden="true" />
                                </a>
                             </li>
                           </ul> 
                           
                       </div> 
                   </div> 
                
               </div>
         </section>

	);
	}
}

export default Contact;