import React, {Component} from 'react';


class Home extends Component{
     render(){
          return(
                   <div>
       <section id="home" className="s-home target-section" data-parallax="scroll" data-image-src="images/eve.jpg" data-natural-width={3000} data-natural-height={2000} data-position-y="center">
          <div className="overlay" />
          <div className="shadow-overlay" />
          <div className="home-content">
            <div className="row home-content__main">
              <h1>
                Manasi Khandekar
              </h1>
              <h2 >Code.Design.Create</h2>
               <h3>Interactive Resume </h3>
              <div className="home-content__buttons">
                <a href="#contact" className="smoothscroll btn btn--stroke">
                  Contact
                </a>
                <a href="#about" className="smoothscroll btn btn--stroke">
                  More About Me
                </a>
              </div>
            </div>
            <div className="home-content__scroll">
              <a href="#about" className="scroll-link smoothscroll">
                <span>Scroll Down</span>
              </a>
            </div>
            <div className="home-content__line" />
          </div> 
          <ul className="home-social">
            <li>
              <a href="http://www.facebook.com/profile.php?id=100009451654660"><i className="fa fa-facebook" aria-hidden="true" /><span>Facebook</span></a>
            </li>
            <li>
              <a href={"#contact"}><i className="fa fa-envelope" aria-hidden="true" /><span>Contact Email</span></a>
            </li>
            <li>
              <a href="http://www.instagram.com/libe_writer/"><i className="fa fa-instagram" aria-hidden="true" /><span>Instagram</span></a>
            </li>
            <li>
              <a href="http://www.linkedin.com/in/manasi-khandekar-627772187"><i className="fa fa-linkedin" aria-hidden="true" /><span>LinkedIn</span>
              </a>
            </li>
          </ul>
        </section><p />
      </div>



          	);
     }
}

export default Home;




